body {
  display: flex;
  justify-content: center;
  align-items: start; /* change center to start */
  height: 100vh;
  margin: 0;
  background-color: #1a1a1a; /* dark grey */
  font-family: Arial, sans-serif;
  color: #f5f5f5; /* light grey */
}

.app-container {
  margin-top: 10%;
  max-height: 90vh;
  overflow-y: auto;
  width: 100%;
  max-width: 500px;
  padding: 20px;
  box-sizing: border-box;
  background-color: #282828; /* slightly darker grey */
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.1); /* white shadow */
}

ul {
  list-style-type: none;
  padding: 0;
}

form {
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  border: none;
  background-color: #3d8b40; /* green */
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #2a5a2e; /* darker green */
}

input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #282828; /* matching with the app-container */
  color: #f5f5f5; /* light grey, matching with the body text */
}
